 export default {
  name: "Mint",
  props: {
    showMint: {
      type: Boolean,
      default: false
    },
    MentList:{
      type: Array,
      default: () => []
    },
    isWhite: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      MintNum: 1,
      visables: this.showMint,
    }
  },
  watch: {
    showMint(n) {
      this.visables = n;
    },
  },
  methods: {
    subtraction() {
      if (this.MintNum > 1) {
        this.MintNum--;
      } else {
        this.$toast(this.$t("polygon.mintAlert"));
      }
    },
    Addition() {
      let MintNum=5;
      if(this.isWhite){
        MintNum=10
      }
      if (this.MintNum < MintNum-this.MentList.length) {
        this.MintNum++;
      }else{
        this.$toast(this.isWhite?this.$t("polygon.whiteAlert1"):this.$t("polygon.whiteAlert2"));
      }
    },
    close() {
      this.visables=false;
      this.$emit("closeMint", false);
      this.MintNum=1;
    },
    MintToken(MintNum) {
      if (MintNum >= 1) {
        this.$emit("closeMint", MintNum)
      } else {
        this.$toast.fail(this.$t("polygon.mintAlert"));
      }
    }
  },
  computed: {
    MATIC() {
      return this.MintNum * 100;
    }
    // MATICPRICE() {
    //   return this.MintNum * 100 * 6.6
    // }
  }
}

